.slideWrap {
  position: relative;
  overflow: hidden; }
  .slideWrap ul.slideAni01 {
    margin: 0;
    padding: 0;
    list-style: none; }
    .slideWrap ul.slideAni01 li {
      background-size: cover;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      animation-iteration-count: infinite;
      animation-duration: 24s; }
      .slideWrap ul.slideAni01 li:nth-child(1) {
        animation-name: slider-zoomin-p3;
        animation-delay: -3s; }
      .slideWrap ul.slideAni01 li:nth-child(2) {
        animation-name: slider-zoomin-p3;
        opacity: 0;
        animation-delay: 5s; }
      .slideWrap ul.slideAni01 li:nth-child(3) {
        animation-name: slider-zoomin-p3;
