        animation-delay: 13s; }
      .slideWrap ul.slideAni01 li img {
        width: 130%; }
  .slideWrap ul.slideAni02, .slideWrap ul.slideAni03, .slideWrap ul.slideAni04 {
    margin: 0;
    padding: 0;
    list-style: none; }
    .slideWrap ul.slideAni02 li, .slideWrap ul.slideAni03 li, .slideWrap ul.slideAni04 li {
      background-size: cover;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      animation-iteration-count: infinite;
      animation-duration: 16s; }
      .slideWrap ul.slideAni02 li:nth-child(1), .slideWrap ul.slideAni03 li:nth-child(1), .slideWrap ul.slideAni04 li:nth-child(1) {
        animation-name: slider-zoomin-p2;
        animation-delay: -3s; }
      .slideWrap ul.slideAni02 li:nth-child(2), .slideWrap ul.slideAni03 li:nth-child(2), .slideWrap ul.slideAni04 li:nth-child(2) {
        animation-name: slider-zoomin-p2;
        opacity: 0;
        animation-delay: 5s; }
      .slideWrap ul.slideAni02 li img, .slideWrap ul.slideAni03 li img, .slideWrap ul.slideAni04 li img {
        width: 130%; }

@keyframes slider-zoomin-p3 {
  0% {
    opacity: 0;
    transform: scale(1); }
  20.83% {
    opacity: 1; }
  33.33% {
    opacity: 1; }
  41% {
    opacity: 0; }
  100% {
    opacity: 0;
    transform: scale(1.1); } }

